  methods: {
    openAndClose(index) {
      var el = document.querySelectorAll(".faq-st-st-bd");
      var inner = document.querySelectorAll(".inner");
      var triangle = document.querySelectorAll(".triangle");
      triangle[index].classList.toggle("isActive");
      if (window.getComputedStyle(el[index]).maxHeight == "0px") {
        el[index].style.maxHeight = inner[index].offsetHeight + 100 + "px";
      } else {
        el[index].style.maxHeight = "0px";
      }
    },
  },
};

