<style lang="less" scoped>
#_faq {
  width: 73%;
  background-color: rgba(0, 117, 249, 1);
  // color: #fff;
  margin: 0 auto;
  box-shadow: 16px 16px 40px 1px rgb(0 0 0 / 6%);
  border-radius: 22px;
  padding-top: 20px;
  min-height: 73vh;
  margin-bottom: 100px;
}
.faq-st {
  min-height: calc(73vh - 20px);
  background-color: rgba(255, 255, 255);
  padding: 5%;
  border-bottom-left-radius: 22px;
  border-bottom-right-radius: 22px;
  &-hd {
    text-align: left;
    margin-bottom: 5.7%;
    font-size: 28px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: inherit;
    line-height: 1.5;
    &-title {
      color: inherit;
    }
  }
  &-st {
    text-align: left;
    &-title {
      display: flex;
      flex-flow: row;
      justify-content: space-between;
      align-items: center;
      justify-content: flex-start;
      cursor: pointer;

      h2 {
        font-size: 22px;
        font-family: PingFangSC-Semibold, PingFang SC;
        font-weight: 600;
        color: inherit;
        line-height: 30px;
        background: none;
      }
      &:hover h2 {
        color: #fff;
        background: linear-gradient(90deg, #2e89e2 0%, #42d7ec 100%);
      }
      .triangle {
        margin-left: 10px;
        display: inline;
        width: 0px;
        height: 0px;
        border-top: 8px solid rgba(0, 117, 249);
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;
      }
      .triangle.isActive {
        transform: rotateX(180deg);
      }
    }
  }
  &-st-bd {
    height: auto;
    max-height: auto;
    transition: all 0.3s ease-in-out;
    overflow: hidden;
    margin: 2.2% 0;
    .inner {
      &-con {
        margin: 1.8% 0;
        &-title {
          display: flex;
          flex-flow: row;
          justify-content: space-between;
          align-items: center;
          justify-content: flex-start;
          align-items: flex-start;
          &-span {
            margin-top: 4px;
            background: rgba(0, 117, 249, 1);
            min-width: 20px;
            height: 20px;
            font-size: 1px;
            line-height: 22px;
            border-radius: 50%;
            text-align: center;
            margin-right: 7px;
            color: #fff;
          }
          &-h3 {
            font-size: 20px;
            font-family: PingFangSC-Semibold, PingFang SC;
            font-weight: 600;
            color: inherit;
            line-height: 1.5;
          }
        }
        &-p {
          padding-left: 30px;
          font-size: 15px;
          font-family: PingFangSC-Regular, PingFang SC;
          font-weight: 400;
          color: #000;
          line-height: 22px;
          padding-top: 5px;
          &-color {
            color: #0080ff;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 992px) {
  #_faq {
    width: 100%;
  }
  .faq-st {
    &-hd {
      font-size: 24px;
    }
    &-st-bd {
      .inner {
        &-con {
          margin: 5% 0;
          &-title {
            &-span {
              width: 20px;
              height: 20px;
              line-height: 22px;
              font-size: 12px;
            }
            &-h3 {
              font-size: 16px;
            }
          }
          &-p {
            font-size: 15px;
          }
        }
      }
    }
  }
}
</style>



